/* eslint-disable no-unused-vars */
/* eslint-disable no-console */

import React, {useEffect, useState, useContext, useMemo} from 'react';
import _ from 'underscore';
import * as moment from 'moment';
import {ContentHeader} from '@components';
import {Button, Modal} from 'react-bootstrap';
import axios from 'axios';
import {useNavigate} from 'react-router-dom';
import DataTable, {createTheme} from 'react-data-table-component';
import {mlCL} from '@app/utils/helpers';
import IconButton from '@mui/material/IconButton';
import {
  Visibility as VisibilityIcon,
  Edit as EditIcon,
  Delete as DeleteIcon
} from '@mui/icons-material';
import {faTimes} from '@fortawesome/free-solid-svg-icons';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import FilterComponent from '@app/components/data-table/FilterComponent';
import GenericoFormBody from '@app/components/forms/GenericoFormBody';
import AppContext from '@app/contexts/AppContext';
import * as AuthService from '@app/services/auth';
import * as Config from '@app/utils/config';

createTheme('solarized', {
  text: {
    primary: 'black'
  },
  background: {
    default: 'white'
  },
  striped: {
    default: '#FAFAFA',
    text: 'rgba(0, 0, 0, 0.87)'
  },
  highlightOnHover: {
    default: 'rgba(0, 0, 0, .7)',
    text: '#FFFFFF'
  }
});

const MostrarCampos = () => {
  const AppCtx = useContext(AppContext);

  const NombreEntidad = 'Campos';
  const NombreEntidadSingular = 'Campo';
  const NombreEntidadMin = NombreEntidad.toLowerCase();

  const navegar = useNavigate();

  const [ViewShow, SetViewShow] = useState(false);
  const handleViewShow = (row, boolDelete) => {
    if (boolDelete) {
      SetId(row._id);
    } else {
      SetRowDataState(row);
    }
    SetRowData(row);
    SetDelete(boolDelete);
    SetViewShow(true);
  };
  const handleViewClose = () => {
    SetViewShow(false);
  };

  const [EditShow, SetEditShow] = useState(false);
  const handleEditShow = (row) => {
    SetRowData(row);
    setBody(row);
    SetId(row._id);
    SetRowDataState(row);
    SetEditShow(true);
  };
  const handleEditClose = () => {
    SetEditShow(false);
  };

  const handleAddNewShow = () => {
    navegar('/agregar-'.concat(NombreEntidadMin));
  };

  const [MessageShow, SetMessageShow] = useState(false);
  const handleMessageShow = () => {
    SetMessageShow(true);
  };
  const handleMessageClose = () => {
    SetMessageShow(false);
  };

  const [isDisabled, setIsDisabled] = useState(false);
  const FormFieldValues = [];

  const [Data, SetData] = useState([]);
  const [RowData, SetRowData] = useState([]);

  const [reqBody, setBody] = useState({
    camCodigo: '',
    camNombre: '',
    camModelo: '',
    camCampo: '',
    camOrden: 10,
    camTipo: 'privado',
    camDueno: AuthService.getProfileId(),
    camEstado: 'activo'
  });

  const [BodyLabels, setBodyLabels] = useState(new Map());
  BodyLabels.set('camCodigo', {label: 'Código', type: 'hidden'});
  BodyLabels.set('camNombre', {label: 'Nombre', type: 'text'});
  BodyLabels.set('camModelo', {
    label: 'Modelo',
    type: 'select',
    value: '',
    options: [
      {value: 'Clientes', name: 'Clientes'},
      {value: 'Vehiculos', name: 'Vehiculos'}
    ]
  });
  BodyLabels.set('camCampo', {label: 'Campo en BD', type: 'hidden'});
  BodyLabels.set('camOrden', {label: 'Orden', type: 'number'});
  BodyLabels.set('camDueno', {label: 'Dueño', type: 'hidden'});
  BodyLabels.set('camTipo', {label: 'Tipo', type: 'hidden'});
  BodyLabels.set('camEstado', {
    label: 'Estado',
    type: 'select',
    value: '',
    options: [
      {value: 'activo', name: 'Activo'},
      {value: 'borrado', name: 'Borrado'}
    ]
  });

  const [Delete, SetDelete] = useState('');
  const [_id, SetId] = useState('');
  const [resMessage, SetResMessage] = useState('');

  const [profileId, SetProfileId] = useState(AuthService.getProfileId());

  const columns = [
    // {
    //   name: 'Código',
    //   selector: (row) => row.camCodigo
    // },
    {
      name: 'Nombre',
      selector: (row) => row.camNombre
    },
    {
      name: 'Estado',
      selector: (row) => row.camEstado
    },
    {
      name: 'Modelo',
      selector: (row) => row.camModelo
    },
    {
      name: 'Campo Base de Datos',
      selector: (row) => row.camCampo
    },
    {
      name: 'Orden',
      selector: (row) => row.camOrden
    },
    {
      name: 'Fecha Creación',
      selector: (row) => (
        <div
          // className="col-2 d-flex align-items-center"
          style={{
            minWidth: 400
          }}
          nowrap="true"
        >
          {moment(row.camFechaCreacion).format('DD-MM-YYYY hh:mm A')}
        </div>
      )
    },
    // {
    //   name: 'Fecha Actualización',
    //   selector: (row) => row.camFechaModificacion
    // },
    {
      name: 'Acciones',
      cell: (row) => (
        // <div className="container">
        <div
          className="col-2 d-flex align-items-center"
          style={{
            minWidth: 150
          }}
          nowrap="true"
        >
          <IconButton
            aria-label="view"
            size="sm"
            color="primary"
            onClick={() => {
              handleViewShow(row, false);
            }}
          >
            <VisibilityIcon />
          </IconButton>
          <IconButton
            aria-label="edit"
            size="sm"
            color="warning"
            onClick={() => {
              handleEditShow(row);
            }}
          >
            <EditIcon />
          </IconButton>
          <IconButton
            aria-label="delete"
            color="error"
            size="sm"
            onClick={() => {
              handleViewShow(row, true);
            }}
          >
            <DeleteIcon />
          </IconButton>
        </div>
        // </div>
      ),
      ignoreRowClick: true,
      allowOverflow: false,
      button: true
    }
  ];

  const [filterText, setFilterText] = useState('');
  const [resetPaginationToggle, setResetPaginationToggle] = useState(false);
  const filteredItems = Data.filter(
    (item) =>
      (item.camCodigo &&
        item.camCodigo.toLowerCase().includes(filterText.toLowerCase())) ||
      (item.camNombre &&
        item.camNombre.toLowerCase().includes(filterText.toLowerCase())) ||
      (item.camModelo &&
        item.camModelo.toLowerCase().includes(filterText.toLowerCase())) ||
      (item.camCampo &&
        item.camCampo.toLowerCase().includes(filterText.toLowerCase()))
  );

  const subHeaderComponentMemo = useMemo(() => {
    const handleClear = () => {
      if (filterText) {
        setResetPaginationToggle(!resetPaginationToggle);
        setFilterText('');
      }
    };

    return (
      <FilterComponent
        onFilter={(e) => setFilterText(e.target.value)}
        onClear={handleClear}
        filterText={filterText}
      />
    );
  }, [filterText, resetPaginationToggle]);

  const onChangeCB = (cbData) => {
    // const {name, value} = cbData;
    const {name, value, campos} = cbData;
    // const {campos} = cbData;

    // if (campos !== undefined) {
    //   campos.forEach((campo) => {
    //     if (campo.nodeName === 'SELECT') {
    //       const opcion = campo.childNodes[campo.selectedIndex];
    //       const value = opcion.getAttribute('value');
    //       const name = campo.getAttribute('name');
    //       reqBody[name] = value;
    // } else {
    // const name = campo.getAttribute('name');
    // const value = campo.getAttribute('value');
    reqBody[name] = value;
    if (name === 'camNombre') {
      reqBody.camCodigo = value
        .toLowerCase()
        .replace(/ /g, '_')
        .replace(/\./g, '');
      reqBody.camCampo = value
        .toLowerCase()
        .replace(/ /g, '_')
        .replace(/\./g, '');
    }
    // }
    // });
    setBody(reqBody);
    // }

    validateFormData(campos);
  };

  const validateFormData = (campos) => {
    let emptyFieldFound = false;

    if (Array.isArray(campos)) {
      Array.from(campos).forEach((campo) => {
        // const id = campo.getAttribute('id');
        // const {id, name, value} = campo;
        const {value} = campo;

        // console.log({id, value});

        // if (!['cliFotoCedula'].includes(id)) {
        //   setFieldsValues(id, value);
        // }

        // if (id === 'email') {
        //   invalidEmailFound = !ValidateEmail(value);
        // }
        // if (['phone_home', 'phone_mobile', 'phone_office'].includes(id)) {
        //   invalidPhoneFound = !ValidatePhone(value);
        // }
        if (value === '' || value === '0' || value === 0) {
          emptyFieldFound = true;
        }
      });

      // setIsDisabled(emptyFieldFound || invalidEmailFound || invalidPhoneFound);
      setIsDisabled(emptyFieldFound);
    }
  };

  const SetRowDataState = (rData) => {
    // console.log('SetRowDataState:', rData);
    _.each(rData, (value, key) => {
      onChangeCB({name: key, value});
    });
  };

  const GetAllData = (ctxProId) => {
    const url = Config.gatDomainName()
      .concat('/')
      .concat(NombreEntidadMin)
      .concat('/listaporduenoprivado/')
      .concat(ctxProId);
    axios
      .get(url)
      .then((response) => {
        const result = response.data;
        const {status, message, data} = result;
        if (status !== 'SUCCESS') {
          mlCL('message:', message);
        } else {
          SetData(data);
        }
      })
      .catch((err) => {
        mlCL('err:', err);
      });
  };

  const submitData = () => {
    const url = Config.gatDomainName()
      .concat('/')
      .concat(NombreEntidadMin)
      .concat('/update');
    const body = {_id, ...reqBody};

    axios
      .post(url, body)
      .then((response) => {
        const result = response.data;
        const {status, message, data} = result;
        if (status !== 'SUCCESS') {
          mlCL('message:', message);
        } else {
          mlCL('data:', data);
          handleEditClose();
          GetAllData(profileId);
          handleMessageShow(SetResMessage(message));
        }
      })
      .catch((err) => {
        mlCL('err:', err);
      });
  };

  const handleDelete = () => {
    const url = Config.gatDomainName()
      .concat(`/`)
      .concat(NombreEntidadMin)
      .concat(`/delete/${_id}`);
    axios
      .get(url)
      .then((response) => {
        const result = response.data;
        const {status, message, data} = result;
        if (status !== 'SUCCESS') {
          mlCL('message:', message);
        } else {
          mlCL('data:', data);
          handleViewClose();
          GetAllData(profileId);
          handleMessageShow(SetResMessage(message));
        }
      })
      .catch((err) => {
        mlCL('err:', err);
      });
  };

  useEffect(() => {
    SetProfileId(AuthService.getProfileId());
  }, []);

  useEffect(() => {
    GetAllData(profileId);
  }, [profileId]);

  return (
    <div>
      <ContentHeader title={'Gestionar '.concat(NombreEntidad)} />
      <section className="content">
        <div className="container-fluid">
          <div className="card">
            <div className="row">
              <div className="mt-3 mb-3 ml-4">
                <Button
                  variant="primary"
                  onClick={() => {
                    handleAddNewShow();
                  }}
                >
                  <i className="fa fa-plu">&nbsp;</i>
                  Agregar Nuevo {NombreEntidadSingular}
                </Button>
              </div>
            </div>
            <div className="row col-12">
              <div className="table-responsive ml-3 mr-1">
                <DataTable
                  // title="Contact List"
                  style={{maxWidth: '100% !important'}}
                  columns={columns}
                  data={filteredItems}
                  // responsive
                  striped
                  pagination
                  paginationResetDefaultPage={resetPaginationToggle} // optionally, a hook to reset pagination to page 1
                  subHeader
                  subHeaderComponent={subHeaderComponentMemo}
                  // onSelectedRowsChange={handleTemplateChange}
                  // selectableRows
                  // selectableRowSelected={rowSelectCritera}
                  selectableRowsRadio
                  selectableRowsHighlight
                  selectableRowsSingle
                  persistTableHead
                />
              </div>
            </div>
            <div className="model-box-view">
              <Modal
                show={ViewShow}
                onHide={handleViewClose}
                backdrop="static"
                keyboard={false}
                size="lg"
              >
                <Modal.Header closeButton>
                  <Modal.Title>Ver Datos {NombreEntidadSingular}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <GenericoFormBody
                    onChangeCB={onChangeCB}
                    RowData={RowData}
                    BodyLabels={BodyLabels}
                    cxcReadOnly="true"
                    cxcAction="viewing"
                  >
                    &nbsp;
                  </GenericoFormBody>
                </Modal.Body>
                <Modal.Footer>
                  <div className="container">
                    <div className="row">
                      <div className="col text-left">
                        {Delete && (
                          <Button
                            type="submit"
                            className="btn btn-danger"
                            onClick={handleDelete}
                          >
                            Borrar {NombreEntidadSingular}
                          </Button>
                        )}
                      </div>
                      <div className="col">&nbsp;</div>
                      <div className="col text-right">
                        <Button variant="secondary" onClick={handleViewClose}>
                          Cerrar
                        </Button>
                      </div>
                    </div>
                  </div>
                </Modal.Footer>
              </Modal>
            </div>
            {/* Modal for Edit employee record */}
            <div className="model-box-view">
              <Modal
                show={EditShow}
                onHide={handleEditClose}
                backdrop="static"
                keyboard={false}
                size="lg"
              >
                <Modal.Header closeButton>
                  <Modal.Title>Editar {NombreEntidadSingular}</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <GenericoFormBody
                    onChangeCB={onChangeCB}
                    RowData={RowData}
                    BodyLabels={BodyLabels}
                    cxcReadOnly="false"
                    cxcAction="editing"
                  >
                    &nbsp;
                  </GenericoFormBody>
                </Modal.Body>
                <Modal.Footer>
                  <div className="container">
                    <div className="row">
                      <div className="col text-left">
                        <Button
                          type="submit"
                          className="btn tn-success"
                          disabled={isDisabled}
                          onClick={submitData}
                        >
                          Guardar
                        </Button>
                      </div>
                      <div className="col">&nbsp;</div>
                      <div className="col text-right">
                        <Button variant="secondary" onClick={handleEditClose}>
                          Cerrar
                        </Button>
                      </div>
                    </div>
                  </div>
                </Modal.Footer>
              </Modal>
            </div>
            <div className="model-box-view">
              <Modal
                show={MessageShow}
                onHide={handleMessageClose}
                backdrop="static"
                keyboard={false}
              >
                <Modal.Header closeButton>
                  <Modal.Title>Respuesta</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                  <div>
                    <div className="">{resMessage}</div>
                  </div>
                </Modal.Body>
                <Modal.Footer>
                  <Button variant="secondary" onClick={handleMessageClose}>
                    Cerrar
                  </Button>
                </Modal.Footer>
              </Modal>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default MostrarCampos;
